import React from "react";
import { useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { getToday } from "../../../commonFuction";

import { axios, urls, baseURL } from "../../../api/axios";
import { UserContext } from "../../../context/user";
import { colorPalette } from "../../../constants/constants";

import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";

import ToggleButton from "@mui/material/ToggleButton";
import ToggleButtonGroup from "@mui/material/ToggleButtonGroup";

// 
const Uploader = ({ image, setImage, sendImage, setSendImage }) => {
  let inputRef; //   Ref 

  // 
  const saveImage = (event) => {
    //event.preventDefault();

    //    
    if (event.target.files[0]) {
      setSendImage((sendImage) => event.target.files[0]);
      //    createObjectURL()   URL 
      URL.revokeObjectURL(image.preview_URL);
      const preview_URL = URL.createObjectURL(event.target.files[0]);
      setImage(() => ({
        image_file: event.target.files[0],
        preview_URL: preview_URL,
      }));
    }
  };

  return (
    <Box
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
      }}
    >
      {/*   */}
      <input
        id="kidprofileurl"
        type="file"
        accept="image/*"
        onChange={saveImage}
        onClick={(event) => (event.target.value = null)} //    file input value       
        ref={(refParam) => (inputRef = refParam)}
        style={{ display: "none" }}
      />
      {/*   */}
      <Avatar
        onClick={() => inputRef.click()}
        alt="kids_picture"
        src={image.preview_URL}
        sx={{
          width: 200,
          height: 200,
          "&:hover": {
            backgroundColor: "#FFFEF4",
          },
        }}
        style={{ cursor: "pointer" }}
      ></Avatar>
    </Box>
  );
};

//    
const ColorToggleButton = ({ gender, setGender }) => {
  const handleChange = (event, newAlignment) => {
    setGender(newAlignment);
  };

  return (
    <ToggleButtonGroup
      color="warning"
      id="kidgender"
      name="kidgender"
      value={gender}
      exclusive
      onChange={handleChange}
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
      }}
      fullWidth
      sx={{ background: "white" }}
    >
      <ToggleButton value="M" id="font_test">
        <Typography id="font_test" component="h6" variant="h6">
          
        </Typography>
      </ToggleButton>
      <ToggleButton value="F" id="font_test">
        <Typography id="font_test" component="h6" variant="h6">
          
        </Typography>
      </ToggleButton>
    </ToggleButtonGroup>
  );
};

const theme = createTheme();

export default function ParentsKids() {
  const navigate = useNavigate();
  const { userNo } = useContext(UserContext);
  // validation
  const initialValues = {
    kidname: "",
    date: getToday(),
  };
  const [formValues, setFormValues] = useState(initialValues);
  const [formErrors, setFormErrors] = useState({});
  const [gender, setGender] = useState("M");
  const [error, setError] = useState("");
  const [sendImage, setSendImage] = useState("");
  const [image, setImage] = useState({
    image_file: "",
    preview_URL: "/images/user.png",
  });

  // (  )
  const validate = () => {
    const errors = {};
    if (!formValues.kidname) {
      alert("  .");
      return false;
    }
    if (formValues.kidname.length >= 10) {
      alert("  10   .");
      return false;
    }
    return true;
  };

  //     
  const handleSubmit = (event) => {
    event.preventDefault();
    //     axios 
    if (validate()) {
      //   Body
      const body = {
        kidName: formValues.kidname,
        kidBirth: formValues.date,
        kidGender: gender,
        kidProfile: image.image_file,
        userNo: userNo,
      };
      //     Multi-part type 
      const config = {
        headers: {
          "Content-type": "multipart/form-data",
        },
      };
      try {
        axios.post(urls.fetchKidsRegister, body, config).then((response) => {
          //   
          if (response.status === 200) {
            navigate("/parents/joincenter", {
              state: { kid_no: response.data.kid_no },
            });
          } else {
            //   
            console.log("  ");
          }
        });
      } catch (err) {}
    }
  };

  //  form  
  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormValues({ ...formValues, [name]: value });
  };

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          onSubmit={handleSubmit}
          sx={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          {/*  title */}
          <Typography id="font_test" component="h1" variant="h4">
            
          </Typography>
          <Typography
            id="font_test"
            component="h1"
            variant="body2"
            color="rgba(0, 0, 0, 0.4)"
          >
              ,   .
          </Typography>
          {/*  form */}
          <Box
            component="form"
            noValidate
            onSubmit={handleChange}
            sx={{ mt: 3 }}
          >
            <Grid container spacing={1}>
              {/*   */}
              <Grid item xs={12} sm={12}>
                <Uploader
                  image={image}
                  setImage={setImage}
                  sendImage={sendImage}
                  setSendImage={setSendImage}
                ></Uploader>
              </Grid>
              {/*    */}
              <Grid item xs={12} sm={12}>
                <TextField
                  required
                  fullWidth
                  id="kidname"
                  label=" "
                  name="kidname"
                  autoComplete="kidname"
                  autoFocus
                  value={formValues.kidname}
                  onChange={handleChange}
                  sx={{ background: "white", mt: 4 }}
                />
              </Grid>
              {/*    */}
              <Grid item xs={12} sm={12}>
                <p id="font_test" align="center">
                    
                </p>
                <TextField
                  required
                  fullWidth
                  id="date"
                  label=""
                  autoComplete="date"
                  name="date"
                  value={formValues.date}
                  onChange={handleChange}
                  autoFocus
                  type="date"
                  sx={{ background: "white" }}
                />
              </Grid>
              <Grid item xs={12} sm={12}>
                <p id="font_test" align="center">
                    
                </p>
                {/* /   */}
                <ColorToggleButton gender={gender} setGender={setGender} />
              </Grid>
              {/*   ! */}
              {/*   */}
              <Grid item xs={12} sm={12}>
                <Button
                  id="font_test"
                  type="submit"
                  fullWidth
                  variant="contained"
                  style={{ background: colorPalette.BUTTON_COLOR }}
                  sx={{ mt: 3, mb: 1 }}
                  onChange={handleChange}
                  size="large"
                >
                  <Typography id="font_test" component="h6" variant="h6">
                    
                  </Typography>
                </Button>
                <Grid container justifyContent="flex-end">
                  <Button
                    variant="body2"
                    id="font_test"
                    style={{ color: "#808080", textDecoration: "none" }}
                    onClick={() => {
                      navigate(-1);
                    }}
                  >
                    
                  </Button>
                </Grid>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}
