import React from "react";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import SearchForm from "./searchform";
import { useState, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { UserContext } from "../../context/user";
import { axios, urls } from "../../api/axios";
import { colorPalette } from "../../constants/constants";

const theme = createTheme();

export default function MasterRegistCenter(props) {
  // 

  const navigate = useNavigate();

  const { userNo, setUserCenter } = useContext(UserContext);

  const obj = {
    centerName: "",
    centerAddr: "",
    centerTel: "",
    userNo: userNo,
  };
  const [address, setAddress] = useState(""); // api  
  const [totalData, setTotalData] = useState(obj); //  
  const [formErrors, setFormErrors] = useState({});

  //    

  const addAddress = (data) => {
    setAddress(data);
  };

  // 
  const validate = () => {
    const errors = {};
    let flag = false;
    if (!totalData.centerName) {
      errors.centerName = "  .";
      flag = true;
    }

    if (!totalData.centerAddr) {
      errors.centerAddr = "  .";
      flag = true;
    }

    if (!totalData.centerTel) {
      errors.centerTel = " .";
      flag = true;
    }
    setFormErrors(errors);
    if (flag) {
      return false;
    }
    return true;
  };
  //   
  const handleChangeCenter = (event) => {
    const middleData = {
      ...totalData,

      centerName: event.target.value,
    };
    setTotalData(middleData);
  };
  //  

  const handleChangeTel = (event) => {
    const middleData = {
      ...totalData,

      centerTel: event.target.value,
    };
    setTotalData(middleData);
  };

  //   
  const handleChangeDetail = (event) => {
    const middleData = {
      ...totalData,

      centerAddr: address.fullAddress + " " + event.target.value,
    };

    setTotalData(middleData);
  };
  //   submit
  const submitAddress = (event) => {
    event.preventDefault();
    if (validate()) {
      axios.post(urls.fetchCentersRegister, totalData).then((response) => {
        if (response.status === 200) {
          setUserCenter(response.data.center_no);
          navigate("/master/managemember");
        }
      });
      //navigate("/master/managemember");
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: "80px",
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Typography
            variant="h4"
            id="font_test"
            sx={{ color: "black", mb: 4 }}
          >
             
          </Typography>
          {/*   */}
          {/* <Typography
            id="font_test"
            component="h6"
            variant="h4"
            sx={{ color: "rgba(0, 0, 0, 0.6)" }}
          >
              
          </Typography>
          <Avatar
            sx={{ width: 250, height: 250 }}
            alt="Academy"
            src="/images/logo.png"
          ></Avatar> */}
          {/*   form */}
          <Box component="form" noValidate sx={{ mt: 3 }}>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={12}>
                {/*    */}
                <TextField
                  required
                  fullWidth
                  id="font_test"
                  placeholder="  "
                  name="centername"
                  onChange={handleChangeCenter}
                  autoComplete="centername"
                  autoFocus
                  sx={{ background: "white" }}
                />
                <p id="font_test">{formErrors.centerName}</p>
              </Grid>
              <Grid item xs={12} sm={12}>
                {/*    */}
                <TextField
                  required
                  fullWidth
                  id="font_test"
                  placeholder=" "
                  name="center_tel"
                  onChange={handleChangeTel}
                  sx={{ background: "white" }}
                />
                <p id="font_test">{formErrors.centerTel}</p>
              </Grid>

              {/* */}
              <Grid sx={{ mt: 10 }} item xs={6} sm={6}>
                <TextField
                  required
                  fullWidth
                  id="font_test"
                  value={address.zonecode || ""}
                  placeholder=""
                  sx={{ background: "white" }}
                />
              </Grid>
              {/*  */}
              <Grid sx={{ mt: 10 }} item xs={6} sm={6}>
                <SearchForm addAddress={addAddress} />
              </Grid>

              {/*     */}
              <Grid item xs={12} sm={12}>
                <TextField
                  required
                  value={address.fullAddress || ""}
                  name="address"
                  id="font_test"
                  placeholder=" "
                  fullWidth
                  sx={{ background: "white" }}
                ></TextField>
                <p id="font_test">{formErrors.centerAddr}</p>
              </Grid>

              {/*  */}
              <Grid item xs={12} sm={12}>
                <TextField
                  fullWidth
                  id="font_test"
                  placeholder=" "
                  onChange={handleChangeDetail}
                  sx={{ background: "white" }}
                />
              </Grid>
            </Grid>

            {/*    */}
            <Button
              fullWidth
              variant="contained"
              onClick={submitAddress}
              style={{ background: colorPalette.BUTTON_COLOR }}
              sx={{ mt: 3, mb: 2 }}
            >
              <Typography id="font_test" component="h6" variant="h6">
                 
              </Typography>
            </Button>
            <Grid container justifyContent="flex-end"></Grid>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}
