// > Page
import GroupManagement from "../../../components/Group/GroupManagement";
import RegistKids from "../../../components/Kids/RegistKids";
import { Box } from "@mui/material";
import { useState, useEffect, React, useContext } from "react";
import { axios, baseURL, urls } from "../../../api/axios";
import { UserContext } from "../../../context/user";

const MasterManageGroup = () => {
  const [server, setServer] = useState([]);
  const [classData, setClassData] = useState([]);
  const [classList, setClassList] = useState([]);

  //     
  const [listItem, setListItem] = useState([]);
  //   
  const [submitList, setSubmitList] = useState([]);
  // /    
  const [selectedItem, setSelectedItem] = useState("1");

  const { userType, userCenter } = useContext(UserContext);

  //     axios
  const getGroupList = () => {
    try {
      axios.get(urls.fetchGroupsList + userCenter).then((response) => {
        if (response.status === 200) {
          setClassData(response.data);
        }
      });
    } catch (e) {
      console.log(e);
    }
  };

  //   ,       
  const getsubmitList = () => {
    if (selectedItem === "1") {
      getKidsList();
    } else if (selectedItem === "2") {
      getTeacherList();
    }
  };

  //       axios
  const getKidsList = () => {
    axios
      .get(baseURL + urls.fetchSubmitWaitKids + userCenter)
      .then((response) => {
        //       
        const newArray = [];
        const newSubmitArray = [];
        response.data.map((data) => {
          //    
          const newObj = {
            no: data.kid_no,
            name: data.kid_name,
            profile_url: data.kid_profile_url,
          };
          newArray.push(newObj);

          //  
          const newSubmitObj = {
            kidNo: data.kid_no,
            groupNo: null,
          };
          newSubmitArray.push(newSubmitObj);
        });

        setListItem(newArray);
        setSubmitList(newSubmitArray);
      });
  };

  //       axios
  const getTeacherList = () => {
    axios
      .get(baseURL + urls.fetchSubmitWaitTeacher + userCenter)
      .then((response) => {
        //       
        const newArray = [];
        const newSubmitArray = [];
        response.data.map((data) => {
          //    
          const newObj = {
            no: data.user_no,
            name: data.user_name,
            profile_url: data.user_profile_url,
          };
          newArray.push(newObj);

          //  
          const newSubmitObj = {
            userNo: data.user_no,
            groupNo: null,
          };
          newSubmitArray.push(newSubmitObj);
        });

        setListItem(newArray);
        setSubmitList(newSubmitArray);
      });
  };

  useEffect(() => {
    getGroupList();
    getKidsList();
  }, []);

  useEffect(() => {
    const newArray = [];
    classData.map((data) => {
      const newObj = {
        id: data.group_no,
        group_name: data.group_name,
      };
      newArray.push(newObj);
    });
    setClassList(newArray);
  }, [classData]);

  return (
    <Box>
      <GroupManagement
        getGroupList={getGroupList}
        classData={classData}
        setClassData={setClassData}
        getsubmitList={getsubmitList}
      />
      <Box style={{ width: "100%", marginTop: "10px", marginBottom: "10px" }} />
      <RegistKids
        classList={classList}
        getGroupList={getGroupList}
        listItem={listItem}
        setListItem={setListItem}
        submitList={submitList}
        setSubmitList={setSubmitList}
        getKidsList={getKidsList}
        getTeacherList={getTeacherList}
        selectedItem={selectedItem}
        setSelectedItem={setSelectedItem}
      />
    </Box>
  );
};

export default MasterManageGroup;
