//    (, , )
const TodayList = ({items}) =>{
  return (
    <ul style={{
      marginTop: "1vh",
      marginLeft: "1vh",
    }}>
      {items &&
        items.split(",").map((item) => (
          <li key={item} id="font_test">
            {item}
          </li>
        ))}
    </ul>
  )
}

export default TodayList