// >> 
// create by 
import { useState, useEffect, useContext } from "react";
import GroupListItem from "../GroupListItem/";
import GroupInsert from "../GroupInsert/";
import Button from "@mui/material/Button";
import { Box, Grid } from "@mui/material";
import * as React from "react";
import ListSubheader from "@mui/material/ListSubheader";
import List from "@mui/material/List";
import ListItemText from "@mui/material/ListItemText";
import Typography from "@mui/material/Typography";
import { UserContext } from "../../../context/user";

const headerList = [
  {
    id: 1,
    text: " ",
    length: 4,
    ml: 2,
  },
  {
    id: 2,
    text: " ",
    length: 4,
    ml: -2,
  },
  {
    id: 3,
    text: " ",
    length: 3,
    ml: -1,
  },
  {
    id: 4,
    text: " ",
    length: 1,
    ml: 0,
  },
];

let newId = 6;

const GroupManagement = (props) => {
  const { getGroupList, classData, setClassData, getsubmitList } = props;
  //     state 
  const [insertFlag, setInsertFlag] = useState(false);

  //  search  useContext
  const { userType } = useContext(UserContext);

  //      
  const insertClicked = () => {
    //     state toggle
    insertComponentToggle();
  };

  //      
  const deleteClicked = (className) => {
    //  state   
    const newClass = classData.filter((data) => data.className !== className);
    setClassData(newClass);
  };

  //    toggle
  function insertComponentToggle() {
    setInsertFlag((insertFlag) => !insertFlag);
  }

  return (
    /*    Part */
    <Box>
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-between",
          marginBottom: "10px",
        }}
      >
        <Box>
          <Typography
            variant="h5"
            id="font_test"
            sx={{ color: "rgba(0, 0, 0, 0.7)" }}
          ></Typography>
        </Box>
        <Box>
          {/*   */}
          <Button
            variant="outlined"
            color="warning"
            onClick={insertClicked}
            size="large"
          >
            <Typography id="font_test"></Typography>
          </Button>
        </Box>
      </Box>
      {/*     */}
      <List
        sx={{
          bgcolor: "#FAF1DA",
          border: "2px solid #ffe2e2",
          borderRadius: "20px",
        }}
      >
        <Grid
          container
          sx={{
            height: "50px",
            background: "#ffe2e2",
            borderTopLeftRadius: "20px",
            borderTopRightRadius: "20px",
            marginTop: "-10px",
          }}
        >
          {headerList.map((list) => (
            <Grid
              item
              xs={list.length}
              sx={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                flexDirection: "row",
              }}
              ml={list.ml}
              key={list.id}
            >
              <ListItemText>
                <Typography
                  variant="h6"
                  id="font_test"
                  style={{ color: "rgba(0, 0, 0, 0.7)" }}
                >
                  {list.text}
                </Typography>
              </ListItemText>
            </Grid>
          ))}
        </Grid>
        {/* state     on/off */}
        {insertFlag && (
          <Box
            sx={{ marginTop: "10px", marginBottom: "10px", marginLeft: "10px" }}
          >
            <GroupInsert
              cancelClicked={insertComponentToggle}
              getGroupList={getGroupList}
            />
          </Box>
        )}
        {/*         */}
        {classData.map((data) => (
          <GroupListItem
            classData={data}
            className={data.group_name}
            key={data.group_no}
            deleteClicked={deleteClicked}
            getGroupList={getGroupList}
            getsubmitList={getsubmitList}
          ></GroupListItem>
        ))}
      </List>
      {/*     */}
    </Box>
  );
};

export default GroupManagement;
