import React, { useContext, useEffect } from "react";
import { useState } from "react";
import { useNavigate } from "react-router-dom";

import { axios, urls, baseURL } from "../../../api/axios";
import { colorPalette } from "../../../constants/constants";
import { UserContext } from "../../../context/user";

import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";

const theme = createTheme();

// 
const Uploader = ({
  defaultImage,
  image,
  setImage,
  sendImage,
  setSendImage,
}) => {
  let inputRef; //   Ref 
  // 
  const saveImage = (event) => {
    //event.preventDefault();

    //    
    if (event.target.files[0]) {
      setSendImage((sendImage) => event.target.files[0]);
      //    createObjectURL()   URL 
      URL.revokeObjectURL(image.preview_URL);
      const preview_URL = URL.createObjectURL(event.target.files[0]);
      setImage(() => ({
        image_file: event.target.files[0],
        preview_URL: preview_URL,
      }));
    }
  };

  return (
    <Box
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
      }}
    >
      {/*   */}
      <input
        id="kidprofileurl"
        type="file"
        accept="image/*"
        onChange={saveImage}
        onClick={(event) => (event.target.value = null)} //    file input value       
        ref={(refParam) => (inputRef = refParam)}
        style={{ display: "none" }}
      />
      {/*   */}
      <Avatar
        onClick={() => inputRef.click()}
        alt="kids_picture"
        src={
          image.preview_URL === "" ? baseURL + defaultImage : image.preview_URL
        }
        sx={{
          width: 200,
          height: 200,
          "&:hover": {
            backgroundColor: "#FFFEF4",
          },
        }}
        style={{ cursor: "pointer" }}
      ></Avatar>
    </Box>
  );
};

export default function Update() {
  const navigate = useNavigate();
  const {
    setUserNo,
    setUserName,
    setUserType,
    setUserPhone,
    setUserCenter,
    setUserGroup,
    setAccessToken,
    setRefreshToken,
    userNo,
  } = useContext(UserContext);

  // validation
  const initialValues = {
    email: "",
    password: "",
    new_password: "",
    new_check_password: "",
    new_username: "",
    new_phone_number: "",
    new_user_profile: "",
  };

  const [formValues, setFormValues] = useState(initialValues);
  const [formErrors, setFormErrors] = useState({});
  const [sendImage, setSendImage] = useState("");
  const [image, setImage] = useState({
    image_file: "",
    preview_URL: "",
  });

  //       
  useEffect(() => {
    try {
      axios
        .get(urls.fetchUsersDetail + userNo)
        .then((response) => {
          if (response.status === 200) {
            const newObj = {
              ...formValues,
              email: response.data.user_email,
              new_username: response.data.user_name,
              new_phone_number: response.data.user_phone,
              new_user_profile: response.data.user_profile_url,
            };
            setFormValues(newObj);
          }
        })
        .catch((error) => {
          console.log(error);
        });
    } catch (e) {
      console.log(e);
    }
  }, []);

  // 
  const validate = () => {
    const errors = {};
    let flag = false;
    if (!formValues.password) {
      errors.password = " .";
      flag = true;
    }
    if (!formValues.new_password || formValues.new_password.indexOf(" ") >= 0) {
      errors.new_password = "  .";
      flag = true;
    }
    if (formValues.new_password.length < 6) {
      errors.new_password = "6  .";
      flag = true;
    }
    if (formValues.new_password !== formValues.new_check_password) {
      errors.new_check_password = "  .";
      flag = true;
    }
    if (!formValues.new_username) {
      errors.new_username = " .";
      flag = true;
    }
    if (!formValues.new_phone_number) {
      errors.new_phone_number = " .";
      flag = true;
    }
    setFormErrors(errors);
    if (flag) {
      return false;
    }
    return true;
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    //    axios 
    if (validate()) {
      const body = {
        userNo: parseInt(userNo),
        userName: formValues.new_username,
        currentPw: formValues.password,
        userPw: formValues.new_password,
        userPhone: formValues.new_phone_number,
        userProfile: image.image_file,
        // userEmail: formValues.email,
      };
      //     Multi-part type 
      const config = {
        headers: {
          "Content-type": "multipart/form-data",
        },
      };
      try {
        const response = await axios.put(
          urls.fetchUsersUpdate + userNo,
          body,
          config,
        );
        setUserNo("");
        setUserName("");
        setUserType("");
        setUserPhone("");
        setUserCenter("");
        setUserGroup("");
        setAccessToken("");
        setRefreshToken("");
        navigate("/"); //     
      } catch (err) {
        const errors = {
          password: "  .",
        };
        setFormErrors(errors);
      }
    }
  };

  //   form  
  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormValues({ ...formValues, [name]: value });
  };

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            mb: 8,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          {/*   */}
          <Avatar
            sx={{ width: 250, height: 250 }}
            alt="Academy"
            src="/images/logo.png"
          ></Avatar>
          <Typography
            component="h1"
            variant="h4"
            id="font_test"
            sx={{ color: "rgba(0, 0, 0, 0.6)" }}
          >
            
          </Typography>
          {/*  form */}
          <Box
            component="form"
            noValidate
            onSubmit={handleSubmit}
            sx={{ mt: 5 }}
          >
            <Grid container spacing={2}>
              {/*  */}
              {/* <Grid item xs={12} sm={12}>
                <TextField
                  required
                  fullWidth
                  autoComplete="email" //     =>   ?
                  name="email"
                  id="email"
                  label=""
                  autoFocus
                  sx={{ background: "white" }}
                />
              </Grid> */}
              {/*   */}
              <Grid item xs={12} sm={12}>
                <Uploader
                  defaultImage={formValues.new_user_profile}
                  image={image}
                  setImage={setImage}
                  sendImage={sendImage}
                  setSendImage={setSendImage}
                ></Uploader>
              </Grid>
              {/*    */}
              <Grid item xs={12} sm={12}>
                <TextField
                  required
                  fullWidth
                  name="password" //    
                  label=" "
                  type="password"
                  id="password"
                  autoComplete="password"
                  value={formValues.password}
                  onChange={handleChange}
                  sx={{ background: "white" }}
                />
                <p>{formErrors.password}</p>
              </Grid>
              {/*    */}
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="new_password"
                  label=" "
                  type="password"
                  id="new_password"
                  autoComplete="new_password"
                  value={formValues.new_password}
                  onChange={handleChange}
                  sx={{ background: "white" }}
                />
                <p>{formErrors.new_password}</p>
              </Grid>
              {/*    */}
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="new_check_password"
                  label="  "
                  type="password"
                  id="new_check_password"
                  autoComplete="new_check_password"
                  value={formValues.new_check_password}
                  onChange={handleChange}
                  sx={{ background: "white" }}
                />
                <p>{formErrors.new_check_password}</p>
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="new_username" //    ???
                  label=""
                  id="new_username"
                  autoComplete="new_username"
                  value={formValues.new_username}
                  onChange={handleChange}
                  sx={{ background: "white" }}
                />
                <p>{formErrors.new_username}</p>
              </Grid>
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="new_phone_number" //    ???
                  label=""
                  id="new_phone_number"
                  autoComplete="phone_number"
                  value={formValues.new_phone_number}
                  onChange={handleChange}
                  sx={{ background: "white" }}
                />
                <p>{formErrors.new_phone_number}</p>
              </Grid>
            </Grid>
            {/*   */}
            <Button
              type="submit"
              fullWidth
              variant="contained"
              style={{ background: colorPalette.BUTTON_COLOR }}
              size="large"
              sx={{ mt: 3, mb: 2 }}
              onChange={handleChange}
            >
              <Typography id="font_test" component="h6" variant="h6">
                
              </Typography>
            </Button>
            {/*   */}
            <Grid container justifyContent="flex-end">
              <Button
                variant="body2"
                id="font_test"
                style={{ color: "#808080", textDecoration: "none" }}
                onClick={() => {
                  navigate(-1);
                }}
              >
                
              </Button>
            </Grid>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}
