import React from "react";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";

import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import FormControl from "@mui/material/FormControl";
import MenuItem from "@mui/material/MenuItem";
import Select from "@mui/material/Select";

import { urls, baseURL } from "../../../api/axios";
import { colorPalette } from "../../../constants/constants";

// 
const Uploader = ({ image, setImage, sendImage, setSendImage }) => {
  let inputRef; //   Ref 

  // 
  const saveImage = (event) => {
    //event.preventDefault();

    //    
    if (event.target.files[0]) {
      setSendImage((sendImage) => event.target.files[0]);
      //    createObjectURL()   URL 
      URL.revokeObjectURL(image.preview_URL);
      const preview_URL = URL.createObjectURL(event.target.files[0]);
      setImage(() => ({
        image_file: event.target.files[0],
        preview_URL: preview_URL,
      }));
    }
  };

  return (
    <Box
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
      }}
    >
      {/*   */}
      <input
        id="kidprofileurl"
        type="file"
        accept="image/*"
        onChange={saveImage}
        onClick={(event) => (event.target.value = null)} //    file input value       
        ref={(refParam) => (inputRef = refParam)}
        style={{ display: "none" }}
      />
      {/*   */}
      <Avatar
        onClick={() => inputRef.click()}
        alt="kids_picture"
        src={image.preview_URL}
        sx={{
          width: 200,
          height: 200,
          "&:hover": {
            backgroundColor: "#FFFEF4",
          },
        }}
        style={{ cursor: "pointer" }}
      ></Avatar>
    </Box>
  );
};

// Select components
const BasicSelectCheck = ({ handleSelect }) => {
  const [value, setValue] = useState("3");
  const selectChange = (event) => {
    setValue((value) => event.target.value);
    handleSelect(event.target.value);
  };

  return (
    <FormControl fullWidth>
      <Select
        value={value}
        onChange={selectChange}
        sx={{ background: "white" }}
        inputProps={{ "aria-label": "Without label" }}
      >
        <MenuItem value="3"></MenuItem>
        <MenuItem value="1"></MenuItem>
        <MenuItem value="2"></MenuItem>
      </Select>
    </FormControl>
  );
};

const theme = createTheme();

export default function SignUp() {
  const navigate = useNavigate();
  // validation
  const initialValues = {
    type: "3",
    email: "",
    password: "",
    check_password: "",
    username: "",
    phone_number: "",
  };

  const [formValues, setFormValues] = useState(initialValues);
  const [formErrors, setFormErrors] = useState({});
  const [sendImage, setSendImage] = useState("");
  const [image, setImage] = useState({
    image_file: "",
    preview_URL: "/images/user.png",
  });

  // 
  const validate = () => {
    const errors = {};
    let flag = false;
    if (!formValues.email || formValues.email.indexOf(" ") >= 0) {
      errors.email = " .";
      flag = true;
    }
    if (formValues.email.indexOf("@") === -1) {
      errors.email = "  .";
      flag = true;
    }
    if (!formValues.password || formValues.password.indexOf(" ") >= 0) {
      errors.password = " .";
      flag = true;
    }
    if (formValues.password.length < 6) {
      errors.password = "6  .";
      flag = true;
    }
    if (formValues.password !== formValues.check_password) {
      errors.check_password = "  .";
      flag = true;
    }
    if (!formValues.username) {
      errors.username = " .";
      flag = true;
    }
    if (!formValues.phone_number) {
      errors.phone_number = " .";
      flag = true;
    }
    setFormErrors(errors);
    if (flag) {
      return false;
    }
    return true;
  };
  //   
  const handleSelect = (val) => {
    setFormValues({ ...formValues, type: val });
  };

  //   
  const handleSubmit = async (event) => {
    event.preventDefault();
    //    axios 
    if (validate()) {
      const body = {
        userType: parseInt(formValues.type),
        userEmail: formValues.email,
        userPw: formValues.password,
        userName: formValues.username,
        userPhone: formValues.phone_number,
        userProfile: image.image_file,
      };
      //     Multi-part type 
      const config = {
        headers: {
          "Content-type": "multipart/form-data",
        },
      };
      try {
        axios
          .post(baseURL + urls.fetchUsersRegister, body, config)
          .then((response) => {
            //   
            if (response.status === 200) {
              navigate("/");
            } else {
              //   
              console.log("  ");
            }
          });
        /*
        const response = await axios.post(
          baseURL + urls.fetchUsersRegister,
          body,
          config,
        );
        console.log(response);
        if (response.status === 200) {
          navigate("/");
        }
        navigate("/");*/
      } catch (err) {
        const errors = {
          email: "  .",
        };
        setFormErrors(errors);
      }
    }
  };

  // form  
  const handleChange = (event) => {
    const { name, value } = event.target;
    setFormValues({ ...formValues, [name]: value });
  };

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            mb: 8,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          {/*   */}
          <Avatar
            sx={{ width: 250, height: 250 }}
            alt="Academy"
            src="/images/logo.png"
          ></Avatar>
          <Typography
            component="h1"
            variant="h5"
            id="font_test"
            sx={{ color: "rgba(0, 0, 0, 0.6)" }}
          >
            
          </Typography>
          {/*  form */}
          <Box
            component="form"
            noValidate
            onSubmit={handleSubmit}
            sx={{ mt: 5 }}
          >
            <Grid container spacing={2}>
              {/*   */}
              <Grid item xs={12} sm={12}>
                <Uploader
                  image={image}
                  setImage={setImage}
                  sendImage={sendImage}
                  setSendImage={setSendImage}
                ></Uploader>
              </Grid>
              {/* , ,   */}
              <Grid item xs={12} sm={12}>
                <BasicSelectCheck
                  handleSelect={handleSelect}
                ></BasicSelectCheck>
              </Grid>
              {/*  */}
              <Grid item xs={12} sm={12}>
                <TextField
                  required
                  fullWidth
                  id="email"
                  label=""
                  name="email"
                  autoComplete="email"
                  autoFocus
                  value={formValues.email}
                  onChange={handleChange}
                  sx={{ background: "white", mt: 2.2 }}
                />
                <p>{formErrors.email}</p>
              </Grid>
              {/*  */}
              <Grid item xs={12} sm={12}>
                <TextField
                  required
                  fullWidth
                  name="password"
                  label=""
                  type="password"
                  id="password"
                  autoComplete="new-password"
                  value={formValues.password}
                  onChange={handleChange}
                  sx={{ background: "white" }}
                />
                <p>{formErrors.password}</p>
              </Grid>
              {/*   */}
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="check_password"
                  label=""
                  id="check_password"
                  type="password"
                  autoComplete="check_password"
                  value={formValues.check_password}
                  onChange={handleChange}
                  sx={{ background: "white" }}
                />
                <p>{formErrors.check_password}</p>
              </Grid>
              {/*   */}
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="username"
                  label=""
                  id="username"
                  autoComplete="username"
                  value={formValues.username}
                  onChange={handleChange}
                  sx={{ background: "white" }}
                />
                <p>{formErrors.username}</p>
              </Grid>
              {/*   */}
              <Grid item xs={12}>
                <TextField
                  required
                  fullWidth
                  name="phone_number"
                  label=""
                  id="phone_number"
                  autoComplete="phone_number"
                  value={formValues.phone_number}
                  onChange={handleChange}
                  sx={{ background: "white" }}
                />
                <p>{formErrors.phone_number}</p>
              </Grid>
            </Grid>
            {/*   */}
            <Button
              type="submit"
              fullWidth
              variant="contained"
              style={{ background: colorPalette.BUTTON_COLOR }}
              sx={{ mt: 3, mb: 2 }}
              onChange={handleChange}
            >
              <Typography id="font_test" component="h6" variant="h6">
                
              </Typography>
            </Button>
            <Grid container justifyContent="flex-end">
              {/*    */}
              <Grid item>
                <Link
                  href="/"
                  variant="body2"
                  id="font_test"
                  style={{ color: "#808080", textDecoration: "none" }}
                >
                   ? 
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}
